.prescription-history {
	width: 100%;
	margin: 0 auto;
	font-size: 0.85em;
	border-collapse: collapse;
}

.prescription-history__header {
	page-break-inside: avoid;
}

.prescription-history__header-cell {
	font-size: .6em;
	padding: 5px;
	border-bottom: 1px solid black;
	vertical-align: top;
	text-align: left;
	text-transform: uppercase;
}

.prescription-history__header-cell--center {
	text-align: center;
}


