.google-map {
	display: block;
	margin: 0 auto;
	max-width: 800px;
	height: 380px;
	position: relative;
	overflow: hidden;
	page-break-inside: avoid;
}

.google-map-marker {
	opacity: .7;
	font-size: 1.5em;
	line-height: 1.5em;
	position: absolute;
	width: 32px;
	height: 32px;
	left: -16px;
	top: -16px;
	border: 3px solid #EEEEEE;
	background-color: #ffffff;
	border-radius: 32px;
	text-align: center;
	color: #333;
	font-size: 16;
	font-weight: bold;
	padding: 4;
}

.google-map-marker.home {
	border-color: #35A81E;
	color: #35A81E;
}

.google-map-marker.pharmacy {
	border-color: #6095F0;
	color: #1F5FCF;
}

.google-map-marker.pharmacy.not-our-provider {
	border-color: #EB1E5C;
	color: #BF1F4F;
}

.google-map-marker.prescriber {
	border-color: #6095F0;
	color: #1F5FCF;
}

.google-map-marker.prescriber.not-our-provider {
	border-color: #EB1E5C;
	color: #BF1F4F;
}

.google-map-marker-text {
	font-weight: bold;
}

.google-map-wrapper {
	display: block;
	overflow: hidden;
}

.gm-style-cc a {
	display: none !important;
}


