.header-prescription-legend {
	margin-bottom: 0;
}

.gantt-chart h3 {
	font-size: 1em;
	font-weight: normal;
	margin: 0;
}

.gantt-chart-container {
	margin-bottom: 0;
	overflow: hidden;
}

.gantt-chart h3.not-our-provider {
	color: #EB1E5C;
}

.pastrx-chart-scrollbar-horizontal {
	display: none;
}

.prescription-legend {
	background-color: #f8f8f8;
	display: inline-block;
	margin: 0 auto;
	border: 1px solid #000;
	font-weight: normal;
	font-size: .5em;
	border-top: 0;
	padding: 3px 15px 3px 0;
	float: right;
}

.prescription-legend span {
	margin-left: 15px;
}

.icon-prescription-legend {
	display: inline-block;
	height: 9px;
	width: 11px;
	vertical-align: text-bottom;
	border-radius: 2px;
}

.icon-opioid {
	background-color: #15a6d8;
}

.icon-benzodiazepine {
	background-color: #e497ab;
}

.icon-stimulant {
	background-color: #9270c1;
}

.icon-sleeping-medication {
	background-color: #647ec8;
}

.icon-other {
	background-color: #A8A8A8;
}


