.patient-alerts {
	margin: .5em 0 0 0;
}

.patient-alerts__row {
	width: 100;
	margin: 0;
	padding: 0;
	list-style: none;
}

.patient-alerts__cell {
	display: inline-block;
	width: calc(100% / 8);

	text-align: center;

	vertical-align: top;
}

.patient-alerts__cell.double {
	width: calc(100% / 4);
}

.patient-alerts__alert-title {
	min-height: 2.3em;
	margin-top: .6em;
	margin-bottom: 0;
	font-size: .7em;
	vertical-align: middle;
}

.patient-alerts__icon {
	font-size: 2.3em;
}

.patient-alerts__icon-container {
	margin-top: .6em;
	padding-top: .6em;
	display: inline-block;
	position: relative;
}

.patient-alerts__count {
	position: absolute;
	top: -0.5em;
	right: -0.7em;
	font-size: .8em;
	line-height: .9em;
	display: inline-block;
	min-width: 19px;
	height: 19px;
	border: 3px solid #eee;
	background-color: #eee;
	border-radius: 32px;
	text-align: center;
	color: #696969;
	font-weight: bold;
	padding: .1em .2em;
	text-shadow: -1px -1px rgba(255, 255, 255, .4);
	padding-top: 1px;
}

.patient-alerts__count.warning {
	color: #fff;
	border: 3px solid #CC023F;
	background-color: #CC023F;
	text-shadow: 1px 1px rgba(0, 0, 0, .4);
}


.alert-medd-chart-container {
	width: 80%;
	display: inline-block;
	margin: 0 auto;
}

.alert-medd-chart-label {
	font-size: .7em;
	text-align: left;
}

.alert-medd-chart-bar {
	font-weight: bold;
	font-size: .7em;
	height: 18px;
	line-height: 18px;
	color: #fff;
	background-color: #000;
	margin-bottom: 5px;
	text-align: right;
	overflow: visible;
	border-top-right-radius: 6px;
	border-bottom-right-radius: 6px;
	background-color: #15a6d8;
	border-left: 3px solid #1183AB;
}

.alert-medd-chart-bar span {
	text-shadow: 1px 1px rgba(0, 0, 0, .3);
	border-top-right-radius: 6px;
	border-bottom-right-radius: 6px;
	display: inline-block;
	padding: 0 7px 0 5px;
	background-color: #34C7FA;
}

.alert-medd-chart-bar.highDose  {
	background-color: #EB1E5C;
	border-left: 3px solid #BD1749;
}

.alert-medd-chart-bar.highDose span {
	background-color: #FF4773;
}





