.provider-card,
.pharmacy-card {
	font-size: .7em;
	width: calc(100% / 3);
	display: inline-block;
	overflow: hidden;
	min-height: 70px;
	padding: 5px 10px 5px 0;
	vertical-align: top;
	text-align: left;
}


.provider-card:nth-child(3n),
.pharmacy-card:nth-child(3n) {
	padding-right: 0;
}

.provider-card__content,
.pharmacy-card__content {
	min-height: 100%;
	display: block;
	padding: 5px 10px;
	border: 1px solid #EBEBEB;
	background-color: #EBEBEB;
}

.not-ours .provider-card__content {
	border-color: #D993A8;
}

.provider-card__provider-name,
.pharmacy-card__provider-name {
	font-size: 1.1em;
	margin-bottom: 5px;
	margin-top: 0;
}

.not-ours .provider-card__provider-name,
.not-ours .provider-card__provider-dea-number,
.not-ours .provider-card__provider-address {
	color: #EB1E5C;
}

.provider-card__provider-dea-number,
.pharmacy-card__provider-dea-number {
	font-size: .8em;
	opacity: .8;
	margin-bottom: 5px;
	margin-top: 0;
}

.provider-card__provider-address,
.pharmacy-card__provider-address {
	margin-top: 0;
	margin-bottom: 8px;
}

.provider-card__provider-address:last-child {
	margin-bottom: 0;
}

.provider-card__icon-wrapper,
.pharmacy-card__icon-wrapper {
	display: table-cell;
	width: 30%;
	
	vertical-align: middle;

	text-align: center;
}

.provider-card__icon,
.pharmacy-card__icon {
	font-size: 28px;
}

.provider-card__provider-info,
.pharmacy-card__provider-info {
	display: table-cell;
	width: 70%;
	
	vertical-align: middle;
}


