.patient-header {
	border-bottom: 2px solid #969696;
}
.patient-header__logo {
  max-width: 50%;
}

.patient-header__row {
  display: block;
  width: 100;
}

.patient-header__cell {
	font-size: .8em;
  display: inline-block;
  width: 33.33%;

  vertical-align: middle;
}

.patient-header__cell .patient-name {
	font-size: 1.9em;
	margin-top: 0;
	margin-bottom: 0;
}

.patient-header__cell .dob {
	margin-top: 0;
	font-size: 1em;
	color: rgba(0, 0, 0, .7);
}

.patient-header__cell strong {
	margin-left: 5px;
}

.patient-header__cell p {
	font-size: .8em;
	margin-top: .3em;
	margin-bottom: .6em;
}



