.prescription-history-row {
	background: white;
}

.prescription-history-row__cell {
	vertical-align: top;
	font-size: .7em;
	padding: 2px 2px;
	text-align: left;
}

.daily-me {
	font-size: 1.1em;
	padding: 3px 5px 2px 5px;
	display: inline-block;
	color: #fff;
	background-color: #15a6d8;
	text-shadow: 1px 1px rgba(0, 0, 0, .3);
	border-radius: 6px;
	min-width: 22px;
}

.daily-me.is-over {
	background-color: #CC023F
}

.alert-summary__prescription-name .daily-me {
	margin-right: 5px;
}

.prescription-history tbody .prescription-history-row:nth-child(2n) td {
	background-color: rgba(0, 0, 0, 0.08);
}

.prescription-history-row__icons {
	padding-right: 10px;
	text-align: right;
	color: #EB1E5C;
	padding-left: 0;
	padding-right: 0;
	min-width: 120px;
}

.prescription-history-row__alert-icon {
	margin: 0 5px;
	font-size: 12px;
}

.prescription-history-row__cell--center {
	text-align: center;
}

.prescription-history-row__cell--error {
	color: #EB1E5C;
}

.prescription-history-row__cell--error::after {
	content: '** Not Ours';
	opacity: .5;
	font-size: .7em;
	display: block;
	margin: 5px 0;
}


