.no-data {
	position: relative;
	font-size: 0.8em;
}

.no-data h1 {
	padding: 2em 0 0.5em 0;
	color: #696969;
	font-size: 1.8em;
	font-weight: normal;
	text-align: center;
}

.no-data-query {
	width: 30%;
	margin: auto;
}

.multipatient-alert {
	position: relative;
	font-size: 0.8em;
}

.multipatient-alert h1 {
	color: #696969;
	font-size: 1.6em;
	font-weight: normal;
	text-align: center;
}

.multipatient-alert div {
	width: 80%;
	margin: auto;
}

.multipatient-alert th, td{
	text-align: left;
	padding: 3px;
	border-bottom: 1px solid #ddd;
}

.matchpatient {
	background-color: #d5f5d8
}


.multistate-alert {
	position: relative;
	font-size: 0.8em;
}

.multistate-alert h1 {
	color: #696969;
	font-size: 1.6em;
	font-weight: normal;
	text-align: center;
}

.multistate-alert div {
	width: 30%;
	margin: auto;
}

.multistate-alert th, td{
	text-align: left;
	padding: 3px;
	border-bottom: 1px solid #ddd;
}

.multistate_error {
	background-color: #eb1e5b;
	font-weight: bolder;
}

.multistate_data {
	background-color: #d5f5d8
}





