
.alert-summary-container {
	padding: 0;
}

.alert-summary {
	display: inline-block;
	padding: 0 .5em .5em 0;
	width: calc(100% / 2);
	vertical-align: top;
}

.alert-summary:nth-child(2n) {
	padding-right: 0;
}

.alert-summary__group {
	border: 1px solid rgba(0, 0, 0, 0.3);
	border-right: none;
	border-left: none;
}

.alert-summary__icon {
	font-size: 25px;
	padding: .2em;
	vertical-align: top;
}

.alert-summary__header {
	text-align: left;
	margin-top: 0;
	margin-bottom: 0;
	line-height: 1em;
	padding: 0 .2em 0 0;
}

.alert-summary__title {
	font-size: .7em;
	padding-top: .6em;
	line-height: .9em;
	display: inline-block;
	max-width: 80%;
}

.alert-summary__sub-title {
	font-size: .8em;
	padding: 1em .3em .1em .3em;
	border-bottom: 1px solid rgba(0, 0, 0, 0.6);
	background-color: rgba(0, 0, 0, 0.05);
}

.alert-summary__title small {
	opacity: .6;
	font-size: .7em;
	line-height: 1.2em;
	display: block;
	font-weight: normal;
}

.alert-summary.Identical .alert-summary__header {
	background-color: #F7F1E6;
}
.alert-summary.Identical .alert-summary__icon {
	color: #fff;
	background-color: #F0CF6C;
}

.alert-summary.HighDose .alert-summary__header {
	background-color: #F7E6E9;
}
.alert-summary.HighDose .alert-summary__icon {
	color: #fff;
	background-color: #F06CA1;
}

.alert-summary.NotOurPrescription .alert-summary__header {
	background-color: #F7EDFA;
}
.alert-summary.NotOurPrescription .alert-summary__icon {
	color: #fff;
	background-color: #CC8FF2;
}

.alert-summary.PrivatePay .alert-summary__header {
	background-color: #E6F7E8;
}
.alert-summary.PrivatePay .alert-summary__icon {
	color: #fff;
	background-color: #4DB848;
}

.alert-summary.MultiplePrescribers .alert-summary__header {
	background-color: #E6F0F7;
}
.alert-summary.MultiplePrescribers .alert-summary__icon {
	color: #fff;
	background-color: #488BB8;
}

.alert-summary.Overlapping .alert-summary__header {
	background-color: #E6F0F7;
}
.alert-summary.Overlapping .alert-summary__icon {
	color: #fff;
	background-color: #488BB8;
}

.alert-summary.WriteFillGap .alert-summary__header {
	background-color: #FCFCE1;
}
.alert-summary.WriteFillGap .alert-summary__icon {
	color: #fff;
	background-color: #C8CC50;
}

.alert-summary.UnexpectedPatient .alert-summary__header {
	background-color: #EBEBEB;
}
.alert-summary.UnexpectedPatient .alert-summary__icon {
	color: #fff;
	background-color: #8F8F8F;
}

.alert-summary__content {
	width: 100%;
	border: 1px solid rgba(0, 0, 0, 0.3);
	padding-bottom: 0;
}

.alert-divider {
	margin: .1em 0;
	border-bottom: 1px dashed rgba(0, 0, 0, 0.05);
}

.alert-summary__icon-wrapper {
	width: 20%;
	display: inline-block;
	vertical-align: middle;
	text-align: center;
}

.alert-summary__narrative {
	padding: 0 .7em;
}

.alert-summary__details {
	padding: 0 .7em 0 .7em;
}

.alert-summary__details-wrapper {
	width: 100%;
	display: inline-block;
	vertical-align: middle;
}

.alert-summary__prescription {
	font-size: .9em;
	padding: 0 .3em .1em .3em;
	border: 1px solid rgba(0, 0, 0, 0.1);
	border-left: none;
	border-right: none;
	border-bottom: none;
	margin-bottom: .3em;
	margin-left: -0.8em;
	margin-right: -0.8em;
	position: relative;
}

.alert-summary__pharmacy-name {
	margin-top: .5em;
	font-size: .7em;
	margin-bottom: .2em;
	font-weight: bold;
	text-transform: uppercase;
	position: relative;
}

.not-our-provider .alert-summary__prescriber-name {
	color: #EB1E5C;
}

.alert-summary__prescriber-name {
	font-size: .7em;
	margin-top: .5em;
	font-weight: bold;
}

.alert-summary__prescription-date {
	text-transform: uppercase;
	font-size: .7em;
	margin-bottom: .4em;
}

.alert-summary__write-fill-gap {
	font-weight: bold;
	padding: .2em .6em .2em .2em;
	margin-right: 5px;
	background-color: rgba(0, 0, 0, 0.05);
}

.alert-summary__write-date,
.alert-summary__fill-date {
	padding-left: 5px;
	text-transform: uppercase;
	font-size: .9em;
	font-weight: normal;
	float: right;
}

.alert-summary__prescription-name {
	line-height: 1.2em;
	padding: .2em .4em .05em .4em;
	margin-left: -0.3em;
	margin-right: -0.3em;
	background-color: rgba(0, 0, 0, 0.03);
	font-size: .7em;
	margin-bottom: .2em;
	overflow: hidden;
}


.alert-summary__split {
	display: inline-block;
	vertical-align: top;
	width: calc(100% / 2);
	padding: .5em .5em .5em 0;
}

.alert-summary__patient {
	font-size: .7em;
}

.alert-summary__patient-type {
	font-size: 1.1em;
	font-weight: bold;
}


